import React from "react";
import { Banner } from "./Source/banner";
import{Header} from "./Source/header";
import { Display } from "./displaymain1";
import {Footer} from './Footer';

import { Categories, Items } from './Source/Items';


export function App(){

    return(<div>
        
        <Header />
        <Banner />
        <Display Categories={Categories} items={Items}/>
        <Footer/>
        
    </div>
    );
}