import React from 'react';
import { Items } from './Source/Items';
import { Display1 } from './Source/display1';


export const Display=(props)=>{
    return(<ul>
           { props.Categories.map(Categorie => {return(
               <Display1 Categorie={Categorie} items={props.items}/>

           )
           }
           )
            }
            
                </ul>
        
    )
}


