
import React from "react";
import bannerimg from './Images/bannerimg.jpg';
import './banner1.css';



export function OfferOfTheDay(props){
    if(props.index1=="Wednessday"){
        return(<div><h2>Offer of the day</h2>
            <img className='bannerImage' src={bannerimg} alt='best offers'></img><br></br>
        <button>Code:Wednes_special</button></div>);
    }
}