import React ,{useState} from "react";
import { Display2 } from "./display2";
import './display1css.css';

export const Display1=(props)=>{

  const [flag,setupdate]=useState(false);
  const falgChange= ()=>{
      setupdate(!flag) ; 
  }

    return(<div>
    <ul className='s1'>
    <li>{props.Categorie.categoryName}</li>
    
    <li>{props.Categorie.discription}</li>
    <li>
    <img src={props.Categorie.imageCat} alt='image'></img></li>
    
    <button id="dis" onClick={falgChange} >Click me  </button> 
    </ul>
    {flag?props.items.map(items => {return(<Display2 items={items}/>)}):console.log(flag)
}
</div>
    

    )
}