import { OfferOfTheDay } from "./offerOfTheDay";

export class Banner extends React.Component{

    constructor(){
        super();
        console.log('constructor');
    }
    render(){ 
        console.log('render called');
        return(
    <div className='banner1'>
        <div className="color2">
        <h1>Cheese Cake Factory</h1>
        <img className='bannerImage' src={bannerimg1} alt='cheese image'></img>
        </div>
        <div className='collor'>
            <h2>Get Special offers every day</h2>
        <OfferOfTheDay index1="Wednessday" />      
        
        </div>
    </div>);
    }
}